<script>
import Card from '../components/Card.vue'
export default {
  name: 'Home',
  components: {
    Card
  },
  data() {
    return {
      personas: [],
      currentPersona: {},
      animating: true,
      personaChanger: Object(),
      lastFifteenPersonas: []
    }
  },
  created: function() {
    this.personas.push(this.cp('a product designer',''));
    [this.cp('a beginner craftsman',''),
    this.cp('a unicorn',''),
    this.cp('a geek',''),
    this.cp('a bump-set-spiker',''),
    this.cp('an amateur chef',''),
    this.cp('a clumsy baker',''),
    this.cp('a boardgame lover',''),
    this.cp('a Muggle-born wizard',''),
    this.cp('a UXer + coder',''),
    this.cp('a Malaysian',''),
    this.cp('an aspiring minimalist',''),
    this.cp('a Marie Kondo fanboy',''),
    this.cp('an Enneagram type 3','3'),
    this.cp('a design systems creator',''),
    this.cp('a plant papa',''),
    this.cp('a daydreamer',''),
    this.cp('your friend',''),
    this.cp('a curious kid',''),
    this.cp('a sushi fiend',''),
    this.cp('a shower singer',''),
    this.cp('an in-flexible yogi','')].
    forEach(x => this.personas.push(x));
    this.currentPersona = this.personas[0];
    this.lastFifteenPersonas.push(0);

    this.personaChanger = setInterval(this.setNewPersona, 8000);
  },
  methods: {
    cp(noun, emoji){
      return {
        noun: noun,
        emoji: emoji
      }
    },
    setNewPersona() {
      clearInterval(this.personaChanger);
      this.animating = false;
      let nextPersona = Math.floor(Math.random() * this.personas.length);
      while (this.lastFifteenPersonas.includes(nextPersona)) {
        nextPersona = Math.floor(Math.random() * this.personas.length);
      }
      this.currentPersona = this.personas[nextPersona];
      this.lastFifteenPersonas.push(nextPersona);
      while(this.lastFifteenPersonas.length > 15){ this.lastFifteenPersonas.shift() }
      setTimeout(()=>{this.animating = true} , 50);
      this.personaChanger = setInterval(this.setNewPersona, 5000 + Math.random()*3000);
    }
  }
}
</script>
